.message {
    width: 100%;
    min-height: 77px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0 220px 80px 0
}

.imageAndText {
    display: flex;
    justify-content: right;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    align-self: flex-end;
}

.text {
    position: relative;
    max-width: 527px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background: #0066CC;
    border-radius: 20px 20px 0 20px;
    padding: 15px;
    font-family: 'Montserrat', sans-serif;
    margin-right: 30px;
}

.text:after {
    position: absolute;
    bottom: 0;
    left: 100%;
    content: '';
    border: 0 solid transparent;
    border-top-width: 20px;
    border-left: 30px #0066CC;
}

.name {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.time {
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
}
.timeContainer{
    width: 48px;
    text-align: center;
}